<template>
    <el-container>
      <el-header>
        <el-card class="box-card">
            <el-row>
                <el-col :span="12" id="titlec">
                    <el-text id="title">服务器监控系统</el-text>
                </el-col>
                <el-col :span="12" id="usericonc">
                    <el-dropdown>
                    <span class="el-dropdown-link">
                    <el-avatar :size="32">{{ $route.params.name.slice(0, 1).toUpperCase() }} </el-avatar>
                    </span>
                    <template #dropdown>
                      <el-dropdown-menu>
                        <el-dropdown-item @click="logout">登出</el-dropdown-item>
                        </el-dropdown-menu>
                    </template>
                    </el-dropdown>
                </el-col>
            </el-row>
        </el-card>
      </el-header>
      <el-main>
        <el-card class="box-card">
            <el-row style="margin-bottom: 10px;">
            <el-col :span="3">
            <el-button plain @click="addServer">
                <el-icon><Plus /></el-icon>
                添加服务器
            </el-button>                
            </el-col>
            <el-col :span="1">
            </el-col>
            <el-col :span="17">
            <el-input v-model="ip" placeholder="Please input server ip address" clearable ><template #prepend>Http://</template></el-input>
            </el-col>
            <el-col :span="1">
            </el-col>
            <el-col :span="2">
                <el-input v-model="port" placeholder="Please input" clearable />
            </el-col>
            </el-row>
            <el-row class="arow" v-for="server in servers" :key="server.ip">
                <el-button  class="iplink" @click="lookserver" round>
                    {{ server.ip }}
                </el-button>
                <el-button round>
                    {{ status }}
                </el-button>
                <el-button round @click="startMonitor(server.ip)">
                    <el-icon><View /></el-icon>
                    <!-- &nbsp;开始监测 -->
                </el-button>
                <el-button round @click="stopMonitor(server.ip)">
                    <el-icon><Hide /></el-icon>
                    <!-- &nbsp;停止监测 -->
                </el-button>
                <el-button round @click="deleteServer(server.ip)">
                    <el-icon><Delete /></el-icon>
                    &nbsp;移除
                </el-button>
            </el-row>
        </el-card>
      </el-main>
    </el-container>
</template>

<script>
import axios from "axios";

export default {
  name: 'Dashboard',
  data() {
    return {
        username: "",
        servers: [],
        ip: "",
        port: 7999,
        status: "未监测",
    }
  },
  mounted() {
    this.accessToken = localStorage.getItem('access_token');
    this.headers = {
        'Authorization': `Bearer ${this.accessToken}`,
        'Content-Type': 'application/json',
    }
    this.getServers()
  },
  methods: {
    logout() {
        localStorage.removeItem('access_token');
        this.$router.push('/');
    },
    addServer() {
        axios.post(`http://127.0.0.1:8000/user/servers?ip=${this.ip}&port=${this.port}`, {}, {headers: this.headers})
        .then(response => {
            console.log('Server response:', response.data);
            this.getServers()
        })
    },
    getServers() {
        axios.get("http://127.0.0.1:8000/user/servers", {headers: this.headers})
        .then(response => {
            console.log('Server response:', response.data);
            this.servers = response.data
        })
        .catch(error => {
          console.log('获取失败');
      });
    },
    lookserver(event) {
        const ip = event.target.innerText
        this.$router.push(`/user/${this.$route.params.name}/${ip}`);
    },
    startMonitor(ip) {
        axios.put(`http://127.0.0.1:8000/user/servers/${ip}/start`, {}, {headers: this.headers})
        .then(response => {
            console.log('Server response:', response.data);
            this.status = "监控中..."
            })
        .catch(error => {console.log('开启失败', error)})
    },
    stopMonitor(ip) {
        axios.put(`http://127.0.0.1:8000/user/servers/${ip}/stop`, {}, {headers: this.headers})
        .then(response => {
            console.log('Server response:', response.data);
            this.status = "未监测"
            clearInterval(this.timerId);
        })
        .catch(error => {console.log('停止失败', error)})
    },
    deleteServer(ip) {
        axios.delete(`http://127.0.0.1:8000/user/servers/${ip}`, {headers: this.headers})
        .then(response => {
            console.log('Server response:', response.data);
            this.getServers()
        })
        .catch(error => {console.log('移除失败', error)})
    },
  }
}
</script>

