<template>
    <el-main class="main">
      <el-row class="row-bg" justify="space-evenly">
          <el-card class="box-card">
            <el-space direction="vertical">
            <el-text class="mx-1" id="title">服务器监控系统</el-text>
            <el-form label-width="60px" :label-position="labelPosition"
                      :model="formLabelAlign" style="max-width: 460px">
              <el-form-item id="form1" label="用户名">
                <el-input type="text" v-model="username" placeholder="Username">
                  <template #suffix>
                    <el-icon class="el-input__icon"><User /></el-icon>
                  </template>
                </el-input>
              </el-form-item>
              <el-form-item id="form2" label="密码">
                <el-input type="password" v-model="password" placeholder="********" :suffix-icon="Calendar">
                  <template #suffix>
                    <el-icon class="el-input__icon"><Unlock /></el-icon>
                  </template>
                </el-input>
              </el-form-item>
            </el-form>
            <el-row class="row-bg" justify="space-evenly">
              <el-button type="primary" @click="login" round>登录</el-button>
              <el-button type="info" @click="register" round>注册</el-button>
            </el-row>
            </el-space>
          </el-card>
      </el-row>
    </el-main>
</template>

<script>
import axios from "axios"
export default {
  name: 'Login',
  data() {
    return {
      username: "",
      password: "",
    }
  },
  methods: {
    login(){
      const formData = new FormData();
      formData.append('username', this.username);
      formData.append('password', this.password);
      formData.append('grant_type', 'password');
      formData.append('scope', 'openid profile');

      axios.post('http://127.0.0.1:8000/token', formData, {
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded',
        }
      })
      .then(response => {
          const accessToken = response.data.access_token;
          localStorage.setItem('access_token', accessToken);
          console.log('登录成功', response.data);
          this.$router.push('/user/' + this.username);
      })
      .catch(error => {
          console.log('登录失败', response.data);
      });
    },
    register() {
      const formData = new FormData();
      formData.append('username', this.username);
      formData.append('password', this.password);
      formData.append('grant_type', 'password');
      formData.append('scope', 'openid profile');

      axios.put('https://127.0.0.1:8000/token', formData, {
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded',
        }
      })
      .then(response => {
          if (response.data.create === "success") {
            console.log('注册成功', response.data);
          }
      })
      .catch(error => {
          console.error('注册失败', error);
      });
    }
  }
}
</script>


