<template>
    <el-container>
      <el-main>
        <el-card class="box-card">
            <el-page-header @back="goBack">
            <template #content>
                <span style="font-size: 14px"> 服务器 {{ $route.params.ip }} </span>
                <el-button plain @click="startMonitor">{{ status }}</el-button>
            </template>
            <div class="mt-4 text-sm font-bold"></div>
            </el-page-header>
            <v-chart class="chart" :option="option" />
        </el-card>
</el-main>
    </el-container>
</template>

<script>
import axios from "axios";
export default {
    name: "Server",
    beforeMount() {
        this.accessToken = localStorage.getItem('access_token');
        this.headers = {
            'Authorization': `Bearer ${this.accessToken}`,
            'Content-Type': 'application/json',
        };
        this.fetchLoads();
    },
    data() {
        return {
            option: this.option,
            status: "开始监测",
        }
    },
    methods: {
        goBack() {
          this.$router.push(`/user/${this.$route.params.name}`)
        },
        fetchLoads() {
            axios.get(`http://127.0.0.1:8000/user/servers/${this.$route.params.ip}`, {headers: this.headers})
                .then(response => {
                    const loads = response.data.loads;
                    const cpuusage = loads.map(function (i) {return i[0];})
                    const timestamp = loads.map(function (i) {return i[1].slice(11);})
                    console.log('Server response:', loads);
                    this.option = {
                        visualMap: {
                            show: false,
                            type: 'continuous',
                            seriesIndex: 0,
                            min: 0,
                            max: 100,
                        },
                        title: {
                            left: 'center',
                            text: '服务器负载情况'
                        },
                        tooltip: {
                            trigger: 'axis'
                        },
                      xAxis: {
                        data: timestamp
                      },
                      yAxis: {
                        type: 'value'
                      },
                      series: [
                        {
                          type: 'line',
                          data: cpuusage,
                          showSymbol: false,
                          areaStyle: {}
                        }
                      ]
                    }
                })
                .catch(error => {
                    console.log('获取失败', error);
                });
        },
        startMonitor(event) {
            const text = event.target.innerText
            if (text === "开始监测") {
                console.log(this.headers)
                axios.put(`http://127.0.0.1:8000/user/servers/${this.$route.params.ip}/start`, {}, {headers: this.headers})
                .then(response => {
                    console.log('Server response:', response.data);
                    this.status = "停止监测"
                        this.timerId = setInterval(() => {
                            this.fetchLoads();
                        }, 1000);
                    })
                .catch(error => {console.log('开启失败', error)})
            }
            else {
                axios.put(`http://127.0.0.1:8000/user/servers/${this.$route.params.ip}/stop`, {}, {headers: this.headers})
                .then(response => {
                    console.log('Server response:', response.data);
                    this.status = "开始监测"
                    clearInterval(this.timerId);
                })
                .catch(error => {console.log('停止失败', error)})
            }
        },
    }
}
</script>

<style scoped>
.el-main {
    justify-content: center;
    margin: 10px;
    margin-top: 20px;
}
.chart {
    height: 450px;
}
.el-button {
    margin-left: 620px;
}
</style>